import { CityTypes, fetchClient, zodCitySchemas } from '@packages/providers';
import { QueryClient, QueryFunction, useQuery } from '@tanstack/react-query';
import { POPULAR_CITIES } from 'application/resources';
import { GetServerSidePropsContext } from 'next';

const queryKey = [POPULAR_CITIES] as const;

type PopularCitiesQueryKey = typeof queryKey;

const queryFn: QueryFunction<CityTypes['popular'], PopularCitiesQueryKey> = async (params) => {
  const { meta } = params;
  const context = meta?.context as GetServerSidePropsContext;
  const {
    data: { data },
  } = await fetchClient.get({ pathname: POPULAR_CITIES }, { context });

  return zodCitySchemas.popular.parse(data);
};

export type UseGetPopularCitiesParams = {
  enabled?: boolean;
};

export const prefetchPopularCities = (
  queryClient: QueryClient,
  context: GetServerSidePropsContext,
) => {
  return queryClient.prefetchQuery({
    queryKey,
    queryFn,
    meta: { context },
  });
};

export const useGetPopularCities = (params: UseGetPopularCitiesParams = {}) => {
  const { enabled } = params;
  return useQuery({
    queryKey,
    queryFn: queryFn,
    enabled,
    refetchOnMount: false,
  });
};

export default useGetPopularCities;
