import { FC, useEffect } from 'react';

import {
  BuilderComponent as KDBuilderComponent,
  BuilderComponentProps as KDBuilderComponentProps,
  useBuilderState,
  useEditor,
} from '@king-david-it/builder';
import { Alert } from '@mui/material';
import { useTranslate } from 'hooks/useTranslate';
import { ErrorBoundary } from 'react-error-boundary';
import { FileRecord } from 'types/index';

import { RootElement } from './elements/RootElement';

const BUILDER_CONTENT_CHANGED = 'builderContentChanged';

//spy to listen builder content changes and send to parent of iframe (ra-lib)
const Spy = () => {
  const { query } = useEditor((_, query) => {
    return {
      query: query.serialize(),
    };
  });

  const content = query.serialize();

  useEffect(() => {
    // second param specifies what the origin of this window must be for the event to be dispatched, either as the literal string "*" (indicating no preference)
    window.parent?.postMessage({ type: BUILDER_CONTENT_CHANGED, content }, '*');
  }, [content]);

  return null;
};

export interface BuilderComponentProps extends Omit<KDBuilderComponentProps, 'files'> {
  files?: FileRecord[];
}

const FallbackMessage = () => {
  const translate = useTranslate({ basePath: 'builder' });

  return <Alert severity="error">{translate('error')}</Alert>;
};

const BuilderComponent: FC<BuilderComponentProps> = (props) => {
  const { children, data, files, ...rest } = props;

  const { isEditing } = useBuilderState();

  if (!data && !isEditing) {
    return null;
  }

  return (
    <ErrorBoundary FallbackComponent={FallbackMessage}>
      <KDBuilderComponent
        data={data}
        // @ts-expect-error type of id can be only string
        files={files}
        spy={<Spy />}
        {...rest}
      >
        {children || <RootElement />}
      </KDBuilderComponent>
    </ErrorBoundary>
  );
};

export default BuilderComponent;
