import { FC, ReactNode } from 'react';

import { Builder, useNode } from '@king-david-it/builder';
import { Box } from '@mui/material';

import { EmptyElement } from './EmptyElement';
import { ComponentSpec } from './types';

export interface RootElementProps {
  children?: ReactNode;
}

export const RootElement: FC<RootElementProps> = (props) => {
  const { children } = props;

  const {
    connectors: { connect },
  } = useNode();

  return <Box ref={(el: HTMLElement) => el && connect(el)}>{children || <EmptyElement />}</Box>;
};

Builder.registerComponent<ComponentSpec>(RootElement, {
  name: 'RootElement',
  displayName: 'Editing area',
  isCanvas: true,
});
